.home_list_content {
  position: relative;
  padding: 0.59259259rem 0;
}
.home_list_content .home_list_item {
  width: 14.81481481rem;
  margin: 0 auto;
  margin-bottom: 1.48148148rem;
  padding-bottom: 0.74074074rem;
  text-align: left;
  border-radius: 0.14814815rem;
  background-color: #fff;
  -webkit-box-shadow: 0 5px 10px 2px rgba(172, 172, 172, 0.2);
          box-shadow: 0 5px 10px 2px rgba(172, 172, 172, 0.2);
}
.home_list_content .home_list_item .home_list_item_img {
  width: 100%;
  height: 7.40740741rem;
  margin: 0.59259259rem auto;
  overflow: hidden;
  border-radius: 0.14814815rem 0.14814815rem 0 0;
}
.home_list_content .home_list_item .home_list_item_img img {
  width: 100%;
}
.home_list_content .home_list_item .home_list_item_img_samll {
  display: inline-block;
  vertical-align: middle;
  width: 1.48148148rem;
  height: 1.48148148rem;
  margin: 0.14814815rem 0.59259259rem;
  border-radius: 50%;
  overflow: hidden;
}
.home_list_content .home_list_item .home_list_item_img_samll img {
  width: 100%;
  height: 100%;
}
.home_list_content .home_list_item .home_list_item_title {
  font-size: 0.74074074rem;
  font-weight: bold;
  margin-left: 0.59259259rem;
}
.home_list_content .home_list_item .home_list_item_detail {
  margin: 0.59259259rem;
  padding-bottom: 0.59259259rem;
  font-size: 0.59259259rem;
  color: #7c7c7c;
  border-bottom: 0.01481481rem solid rgba(71, 71, 71, 0.8);
}
.home_list_content .home_list_item .home_list_item_city {
  vertical-align: middle;
  font-size: 0.51851852rem;
  color: #7c7c7c;
}
.home_list_content .prev_page,
.home_list_content .next_page {
  display: inline-block;
  width: 2.22222222rem;
  height: 2.22222222rem;
  margin: 0.2962963rem 1.48148148rem;
  color: #fff;
  outline: none;
  border: none;
  border-radius: 50%;
  background-color: #15bf6f;
  line-height: 2.22222222rem;
}
.home_list_content .prev_page a,
.home_list_content .next_page a {
  display: block;
  color: #fff;
}
.home_list_content .prev_page a i,
.home_list_content .next_page a i {
  font-size: 1.18518519rem;
}



