.introduce_wrap {
  position: absolute;
  top: 0;
  visibility: hidden;
  -webkit-transform: scale3d(0, 0, 0);
          transform: scale3d(0, 0, 0);
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0);
  -webkit-transition: background-color 0.5s linear, -webkit-transform 0.2s linear;
  transition: background-color 0.5s linear, -webkit-transform 0.2s linear;
  -o-transition: transform 0.2s linear, background-color 0.5s linear;
  transition: transform 0.2s linear, background-color 0.5s linear;
  transition: transform 0.2s linear, background-color 0.5s linear, -webkit-transform 0.2s linear;
}
.introduce_wrap.showMask {
  visibility: visible;
  -webkit-transform: scale3d(1, 1, 1);
          transform: scale3d(1, 1, 1);
  background-color: rgba(0, 0, 0, 0.5);
}



