.header_search {
  height: 2.96296296rem;
  background-color: #0eb467;
  overflow: hidden;
}
.header_search .header_search_wrap {
  position: relative;
  width: 75%;
  height: 1.77777778rem;
  margin: 0 auto;
  margin-top: 0.59259259rem;
}
.header_search .header_search_wrap form {
  width: 100%;
  height: 100%;
}
.header_search .header_search_wrap form input {
  width: 100%;
  height: 100%;
  font-size: 0.59259259rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: none;
  outline: none;
  border-radius: 0.88888889rem;
  padding-left: 1.77777778rem;
}
.header_search .header_search_wrap .search_icon {
  position: absolute;
  top: 0.44444444rem;
  color: #acacac;
}
.header_search .header_search_wrap .search_icon i {
  font-size: 0.74074074rem;
}
.header_search .header_search_wrap .search_icon:nth-of-type(1) {
  left: 0.59259259rem;
}
.header_search .header_search_wrap .search_icon:nth-of-type(2) {
  right: 0.59259259rem;
}
.introduce_header_wrap {
  position: relative;
  height: 2.96296296rem;
  background-color: #15bf6f;
  line-height: 2.96296296rem;
  text-align: center;
}
.introduce_header_wrap i {
  position: absolute;
  font-size: 1.48148148rem;
  color: #fff;
  top: 0;
  left: 0.74074074rem;
}
.introduce_header_wrap h2 {
  color: #fff;
  font-size: 0.88888889rem;
  font-weight: normal;
}



