.introduce_content_wrap {
  position: absolute;
  background-color: #fff;
  top: 8%;
  width: 100%;
  height: 84%;
  border-radius: 0.44444444rem;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.introduce_content_wrap > div {
  width: 90%;
  margin: 1.18518519rem auto;
}
.introduce_content_wrap .introduce_content_name {
  margin-top: 0;
}
.introduce_content_wrap h2 {
  font-size: 0.88888889rem;
}
.introduce_content_wrap h3 {
  font-size: 0.66666667rem;
}
.introduce_content_wrap p {
  font-size: 0.59259259rem;
}
.introduce_content_wrap .introduce_content_pic {
  width: 90%;
  margin: 0.74074074rem auto 0 auto;
}
.introduce_content_wrap .introduce_content_pic img {
  width: 100%;
}



